<!--  -->
<template>
  <div>
      <Zi bool1="true"  goback="true" con="重置密码">
        <h1 @click="$router.go(-1)" slot="left">
        <
      </h1>
      </Zi>
        <div class="cont">
            <div>
              <input type="text" placeholder="账号">
            </div>
             <div>
              <input  type="text" placeholder="旧密码" />
            </div>
                <div>
              <input  type="text" placeholder="请输入新密码" />
            </div>
                <div>
              <input  type="text" placeholder="请确认密码" />
            </div>
             <div>
              <input type="text" placeholder="验证码">
              <div class="yan">
               <span class="span">{{this.num}}</span>
               <span>
                  <p  @click="reset">看不清</p>
                  <p class="reset" @click="reset">换一种</p>
                 </span> 
              </div>
            </div>
        </div>
        <button>确认修改</button>
  </div>
</template>

<script>
import Zi from "../../../components/header"
export default {
  data () {
    return {
       value1: true,
       value2:false,
       num:"",
       bool:true


    };
  },

  components: {Zi},

  computed: {},

  mounted(){
    var a=""
   for(var i=0;i<4;i++){
     a+=parseInt(Math.random()*10)
   }
   this.num=a
  },

  created(){},

  methods: {
    // 验证码
    reset(){
      var a=""
   for(var i=0;i<4;i++){
     a+=parseInt(Math.random()*10)
   }
   this.num=a
    },
  
  }
}

</script>
<style scoped lang="scss">
header{
     h1{color: white}
  display: flex;
  flex-wrap: wrap;
 color: #fff;
    height: 1rem;
  background: #3190e8;
  font-size: 2rem;
  width: 100%;
 span{
   display: block;
   font-size: 0.3rem;
   line-height: 1rem;
   width: 45%;
 }
 .deng{
     font-size:0.3rem;
     width: 50%;
     font-weight: bold
 }

}
.cont{
  &>div{
    background: white;
    margin-bottom: 1px;
    width: 100%;
    height: 1rem;
    border-bottom: 1px solid #d4d4d4;
    input{
      height:0.5rem;
      margin-left: 0.4rem;
      margin-top: 0.25rem;
      font-size: 0.3rem; 
      color: #ccc;
      border: 1px solid blue;
      border: none
    }
   div{
     float: right;
   margin-top: 0.2rem;
  .el-switch__core{
        width:1rem!important;
  }
   }
  }
}

button{
  display: block;
  height: 0.6rem;
 background-color: #4cd964;
 width: 90%;
 font-size: 0.3rem;
 color: white;
 margin: 0.3rem auto
}
.yan{
  display: flex;
  span{
    display: block;
  }
}
.reset{
 color: #3b95e9;
}
.reset1{
  float:right;
}
.span{
  line-height:0.5rem;
  margin-right: 0.2rem
}
</style>


